<template>
  <div class="flex-column vfull">
    <div>
      <div class="flex-row topbar">
        <button v-if="isLoaded" @click="choosePassage"><i class="fa fa-book"></i> Passage</button>
        <p v-if="!isLoaded">Loading...</p>
        <div class="flex-one"></div>
        <div class="effects">
          <div class="effect glissando" :class="{current: activeEffect === 'glissando'}" @click="activeEffect = 'glissando'"><span>G</span></div>
          <div class="effect strings" :class="{current: activeEffect === 'strings'}" @click="activeEffect = 'strings'"><span>S</span></div>
        </div>
      </div>
      <div class="settings">
        <input type="number" name="tempo" v-model="tempo" />
      </div>
    </div>

    <div class="flex-one scrolly">
      <div class="words">
        <h3>{{book}} {{chapter}}</h3>
        <div v-for="(word, index) in words" class="word" :class="wordClasses(word, index)" @click="wordPressed(word, index, $event.target)">{{word.text}} </div>
      </div>
    </div>

    <div class="controls">
      <div class="flex-row">
        <button v-if="isLoaded && !isPlaying" @click="play"><i class="fa fa-play fa-2x"></i></button>
        <button v-if="isPlaying && !isPaused" @click="pause"><i class="fa fa-pause fa-2x"></i></button>
      </div>
    </div>
  </div>
</template>

<script>
import bibleLoader from '../../static/js/helpers/bibleloader'
import midi from '../../static/js/helpers/midiword'

export default {
  name: 'home',
  data () {
    return {
      tempo: 120,
      verses: undefined,
      words: undefined,
      book: undefined,
      chapter: undefined,
      activeEffect: 'glissando',
      currentWord: -1,
      isLoaded: false,
      isPlaying: false,
      isPaused: false
    }
  },
  watch: {
    tempo (value) {
      midi.tempo = parseInt(value)
    }
  },
  methods: {
    choosePassage () {
      this.$router.push('/passage')
    },
    wordClasses (word, index) {
      var classes = []
      if (index === this.currentWord) {
        classes.push('current')
      }
      if (word.glissando) {
        classes.push('glissando')
      }
      if (word.strings) {
        classes.push('strings')
      }
      return classes
    },
    wordPressed (word, index, element) {
      word[this.activeEffect] = !word[this.activeEffect]
      element.classList.toggle(this.activeEffect)
    },
    play () {
      this.currentWord += 1
      if (this.isPaused) {
        this.isPaused = false
        return
      }
      this.isPlaying = true
      var wordEntry = this.words[this.currentWord]
      var word = wordEntry.text
      var t = midi.msPerBeat() * (word.length / 2 > 4 ? 4 : (word.length / 2 > 2 ? 2 : 1))
      setTimeout(this.play, t)
      if (word.endsWith('.')) {
        midi.playTonic()
      } else if (word.endsWith(',')) {
        midi.playFifth()
      } else if (wordEntry.glissando) {
        midi.glissando(word)
      } else if (wordEntry.strings) {
        midi.playWord(word.substring(0, 1), 1)
        midi.playWord(word)
      } else {
        midi.playWord(word)
      }
    },
    pause () {
      this.isPaused = true
      this.isPlaying = false
    }
  },
  mounted () {
    const book = this.$route.query.book
    const chapter = this.$route.query.chapter
    if (book && chapter) {
      const self = this
      self.isLoaded = false
      bibleLoader.load(book, chapter, 'esv', verses => {
        self.book = book
        self.chapter = chapter
        self.verses = verses
        self.words = []
        verses.forEach(v => {
          self.words = self.words.concat(v.text.split(' ')
          .map(w => {
            return {text: w}
          }))
        })
        self.isLoaded = true
      })
    }
  }
}
</script>

<style lang="less" scoped>
@import '../../static/less/flex';
@import '../../static/less/common';

@color-blue: #3388cc;

.topbar {
  border-bottom: solid 1px @color-blue;
  padding: 10px;
  align-items: center;
  button {
    background-color: transparent;
    border: solid 1px @color-blue;
    cursor: pointer;
    border-radius: 4px;
    padding: 10px;
    color: white;
    font-size: 14px;
    outline: none;
    &:hover {
      text-decoration: underline;
    }
  }
}
.settings {
  padding: 10px;
  border-bottom: solid 1px @color-blue;
  input {
    font-size: 16px;
    border-radius: 4px;
    border: solid 1px @color-blue;
    background-color: transparent;
    color: white;
    padding: 8px;
  }
}
.controls {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: transparent;
  background: linear-gradient(transparent, black);
  .flex-row {
    justify-content: center;
    width: 100%;
  }
  button {
    background-color: @color-back;
    border: none;
    border-top-left-radius: 50px;
    border-top-right-radius: 50px;
    box-shadow: 0px -2px 5px black;
    padding: 15px;
    min-width: 100px;
    color: white;
    font-size: 14px;
    outline: none;
    &:hover {
      color: @color-blue;
    }
  }
}
.effects {
  display: flex;
  flex-direction: row;
  align-items: center;
  .effect {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 40px;
    width: 40px;
    margin-right: 10px;
    border-radius: 25px;
    border: solid 2px transparent;
    color: white;
    font-size: 18px;
    font-weight: bold;
    cursor: pointer;
    &.current {
      border: solid 2px #7cf;
      font-size: 22px;
      text-shadow: 0px 0px 3px black;
      box-shadow: 0px 0px 9px 1px #ddddff;
    }
    &.glissando {
      background-color: @color-blue;
    }
    &.strings {
      background-color: #ff9911;
    }
  }
}
.words {
  line-height: 28px;
  font-size: 17px;
  padding: 10px;
  padding-bottom: 100px;
  h3 {
    text-align: center;
    color: #bbb;
  }
  .word {
    display: inline;
    transition: color 0.2s;
    border: solid 2px transparent;
    cursor: pointer;
    &.current {
      color: #ffaa00;
    }
    &.glissando {
      border-bottom: solid 2px @color-blue;
    }
    &.strings {
      background: linear-gradient(transparent, #ff9911);
      border-top-color: black;
      text-shadow: 0px 0px 3px black;
      border-radius: 5px;
    }
  }
  .reference {
    color: #999;
    vertical-align: super;
    font-size: 11px;
  }
}
</style>



