import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import PassageChooser from '@/components/PassageChooser'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/passage',
      name: 'Passage',
      component: PassageChooser
    }
  ]
})



