<template>
  <div id='app'>
    <router-view></router-view>
    <div class='loading visible'>LOADING...</div>
  </div>
</template>

<script>
export default {
  name: 'app',
  mounted () {
    const self = this
    window.MIDI.loadPlugin({
      soundfontUrl: './static/sounds/',
      instruments: ['piano', 'cello'],
      onprogress: (state, progress) => {
        if (progress === 1) {
          self.$el.querySelector('.loading').classList.remove('visible')
        } else {
          self.$el.querySelector('.loading').classList.add('visible')
        }
        console.log(state, progress)
      },
      onsuccess: () => {
        console.log('sound loaded')
        window.MIDI.setVolume(0, 127)
        window.MIDI.programChange(1, window.MIDI.GM.byName['cello'].number)
      }
    })
  }
}
</script>

<style lang="less">
@import '../static/less/common';

html {
  height: 100%;
}
body {
  background-color: @color-back;
  color: #efefef;
  height: 100%;
  margin: 0;
}
</style>

<style lang="less" scoped>
#app {
  height: 100%;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.loading {
  display: none;
  position: fixed;
  top: 0;
  left:0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.6);
  color: white;
  text-align: center;
  padding: 30px 10px;
  &.visible {
    display: inherit;
  }
}
</style>



